.navBar {
    display: flex;
    height: 50px;
    width: 100%;
    position: fixed;
    justify-content: space-around;
    align-items: center;
}

.navOpen {
    display: flex;
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: #4a4564;
    flex-direction: column;
    align-items: center;
}

.navLink {
    display: flex;
    font-size: 2em;
    font-weight: 400;
    color: #f1f1f1;
    padding-top: 90px;
}

.navLink:hover {
    cursor: pointer;
}

